class Gameboard extends React.Component {
  render() {
    return (
      <div className="gameboard">
        {this.props.pastGuesses.map((wordInfo, index) => {
          const satisfaction = this.props.satisfaction;
          const lettersSatisfied = (satisfaction === null ? ALL_SATISFIED : satisfaction[index]);
          const left = (
            wordInfo.pattern === ALL_HIT 
            ? <TurnNumber turnNumber={this.props.pastGuesses.length} /> 
            : (lettersSatisfied === ALL_SATISFIED ? '' : <div className="trust-button arrow">{"\u25B6"}</div>));
          return (
            <GameboardRow key={index} rowNum={index} wordInfo={wordInfo} relativeTrust={this.props.pastRelativeTrust[index]} trust={this.props.relativeToAbsoluteTrust[this.props.pastRelativeTrust[index]]} toggleTrust={this.props.toggleTrust} lettersSatisfied={lettersSatisfied} left={left} />
            );})}
        {this.props.isWin ? <div className="message win">You Win!</div> : <GameboardRow wordInfo={{guessedWord: this.props.currentGuess, pattern: ALL_BLANK}} lettersSatisfied={ALL_SATISFIED} isGivenUp={this.props.isGivenUp} left={<TurnNumber turnNumber={this.props.pastGuesses.length + 1} />} />}
        {this.props.message ? <div className="message">{this.props.message}</div> : ''}
        {this.props.isGivenUp || this.props.isWin ? <GameOverRow shareCopyStatus={this.props.shareCopyStatus} doShare={this.props.doShare} doNewGame={this.props.doNewGame} /> : ''}
      </div>
    )
  }
}

class GameboardRow extends React.Component {
  constructor(props) {
    super(props);
    this.onClick = this.onClick.bind(this);
  }

  render() {
    const guessedWord = this.props.wordInfo.guessedWord;
    // console.log('wordInfo', this.props.wordInfo)
    const pattern = this.props.wordInfo.pattern;
    // const [word, pattern] = this.props.wordInfo;
    const fullRow = [...guessedWord].concat(Array(WORD_LENGTH - guessedWord.length).fill(' '));
    // let fullPattern;
    // if (pattern.length === WORD_LENGTH) {fullPattern = pattern} else {fullPattern = ALL_BLANK}
    // console.log('isGivenUp', this.props.isGivenUp)
    return (
      <div className={"gameboard-row " + CSS_TRUST[this.props.relativeTrust] + (this.props.isGivenUp ? ' given-up' : '')} onClick={this.onClick}>
        {this.props.left ? this.props.left : <div className="trust-button" />}
        {fullRow.map((letter, index) => {return (<GameboardSquare key={index} letter={letter} letterSatisfied={this.props.lettersSatisfied[index]} aim={pattern[index]} />);})}
        <TrustButton trust={this.props.trust} />
      </div>
    )
  }

  onClick() {
    if (this.props.toggleTrust) {
      this.props.toggleTrust(this.props.rowNum);
    }
  }
}

