class NumberInput extends React.Component {
  constructor(props) {
    super(props);
    this.decrement = this.decrement.bind(this);
    this.increment = this.increment.bind(this);
  }
  render() {
    const value = this.props.value;
    return (
      <div className="number-input">
        <button className={(value === 0 || this.props.off) ? "off" : ""} onClick={this.decrement}>-</button>
        <div className="number-input-value">{value}</div>
        <button className={this.props.off ? "off" : ""} onClick={this.increment}>+</button>
      </div>
    );
  }

  decrement() {
    this.props.add(-1);
  }

  increment() {
    this.props.add(1);
  }
}

