  computeRelativeToAbsoluteTrust(useFancyTrust, pastRelativeTrust, maxLies) { // does not use "this"
    if (useFancyTrust) {
      let numTrustLess = 0;
      pastRelativeTrust.forEach((relativeTrust) => {
        if (relativeTrust === TRUST_LESS) {
          numTrustLess += 1;
        }
      })
      const relativeToAbsoluteTrust = {};
      if (numTrustLess < maxLies) {  // should be maxLies
        relativeToAbsoluteTrust[TRUST_MORE] = UNSURE;
      } else {
        relativeToAbsoluteTrust[TRUST_MORE] = CONFIDENT;
      }
      if (numTrustLess <= maxLies) {  // should be maxLies
        relativeToAbsoluteTrust[TRUST_LESS] = DISTRUST;
      } else {
        relativeToAbsoluteTrust[TRUST_LESS] = UNSURE;
      }
      return relativeToAbsoluteTrust;
    } else {
      return {[TRUST_MORE]: CONFIDENT, [TRUST_LESS]: DISTRUST};
    }
  }

